<template>
  <div id="a">
    <ul>
    	<li v-for='item,index in da' :key='index'>
        <input type="checkbox" :checked="item.sele">
        <p class="name">{{item.title}}</p>
        <p class="price">{{item.price}}</p>
        <p class="adds">
          <span class="add" @click='add(index)'>+</span>
          <span>{{item.num}}</span>
          <span class="remove" @click='remove(index)'>-</span>
        </p>
      </li>
    </ul>
  </div>
</template>
<script>
export default{
  data(){
    return {

    }
  },
  computed: {
    da(){
      return this.$store.state.carList;
    }
  },
  methods: {
    add(index){
      this.$store.commit('add',index);
    },
    remove(index){

    }
  }
}
</script>

<style>
  li{
    display: flex;
  }
  li p{
    margin-right: 20px;
  }
</style>



